<template>
  <v-dialog width="350px" persistent v-model="editDialog">
    <v-btn accent slot="activator">
      Edit Time
    </v-btn>
    <v-card>
      <v-container>
        <v-layout row wrap>
          <v-flex xs12>
              <v-card-title>Edit Event Time</v-card-title>
          </v-flex>
        </v-layout>
        <v-divider></v-divider>
        <v-layout row wrap>
          <v-flex xs12>
            <v-time-picker v-model="editableTime" style="width: 100%" actions format="24hr">
              <template slot-scope="{save, canel}">
                <v-btn flat @click.native="onConfirmChanges">Confirm</v-btn>
                <v-btn flat @click.native="editDialog = false">Close</v-btn>
              </template>
            </v-time-picker>
          </v-flex>
        </v-layout>
      </v-container>
    </v-card>
  </v-dialog>
</template>

<script>
  export default {
    props: ['event'],
    data () {
      return {
        editDialog: false,
        editableTime: null
      }
    },
    methods: {
      onConfirmChanges () {
        const newDate = new Date(this.event.date)
        const hours = this.editableTime.match(/^(\d+)/)[1]
        const minutes = this.editableTime.match(/:(\d+)/)[1]
        newDate.setHours(hours)
        newDate.setMinutes(minutes)
        this.$store.dispatch('updateEvent', {
          id: this.event.id,
          date: newDate
        })
      }
    },
    created () {
      this.editableTime = new Date(this.event.date).toTimeString()
    }
  }
</script>



